@import "../../../../styles/setup.scss";

.node-metric {
  border: 1px solid $stratos-border-color;
  border-radius: 12px;

  font-style: normal;
  font-weight: normal;
  font-family: $equal-font;

  .metric-title-container {
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 10px;

    .metric-title {
      font-size: 16px;
      line-height: 24px;
    }
  }

  .metric-data {
    padding-left: 15px;
    padding-bottom: 10px;

    .mertic-icon-container {
      margin-right: 15px;
    }

    .metric-info {
      font-size: 26px;
      line-height: 48px;
    }
  }
}
