@import "../../../styles/setup.scss";

.hero {
  max-height: 60px;
  &.app-header {
    background-color: $stratos-header-color;
    color: white;
    padding: 1rem 1.5rem;
  }

  .hero-body {
    padding: 0;
    padding-left: 1.5rem;
    display: flex;
    align-items: center;
  }

  .app-logo-container {
    display: flex;
    align-items: center;
    .logo-title {
      margin-left: 10px;
    }
  }
}
