@import "../../../../styles/setup.scss";

.peers-info-container {
  th {
    .sort-icon {
      color: $stratos-green-ligth;
      margin-left: 5px;
      justify-content: center;
      display: flex;
      align-items: center;
    }
    p {
      color: $stratos-green-ligth;
      font-size: 14px;
      line-height: 18px;
      font-style: normal;
      font-weight: normal;

      // mobile
      @media only screen and (max-width: $mobile-screen-width) {
        display: flex;
        text-align: center;
        margin-top: auto;
        align-items: center;
        min-height: 50px;
      }
    }
  }
}
