@import "../../../../styles/setup.scss";

.traffic-metric {
  border-radius: 12px;
  margin-bottom: 5px;
  font-style: normal;
  font-weight: normal;
  font-family: $equal-font;

  &.is-inbound {
    background-color: $stratos-inbound-trafic-color;
    border: 1px solid $stratos-inbound-trafic-color;
  }
  &.is-outbound {
    background-color: $stratos-outbound-trafic-color;
    border: 1px solid $stratos-outbound-trafic-color;
  }

  .metric-title-container {
    color: white;
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 10px;

    .metric-title {
      font-weight: 500;
      font-size: 32px;
      line-height: 48px;
    }
  }

  .metric-data {
    padding-left: 12px;
    padding-right: 12px;
    padding-bottom: 10px;

    color: white;
    font-weight: 500;
    .metric-speed-container {
      .speed-current {
        font-size: 48px;
        line-height: 60px;
      }
      .speed-max {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 5px;
      }
      margin-bottom: 5px;
    }

    .metric-label-container {
      padding-left: 5px;

      .speed-current-label {
        font-size: 16px;
        line-height: 24px;
      }
      .speed-max-label {
        font-size: 16px;
        line-height: 24px;
      }
    }
  }
}
