@import "../../../styles/setup.scss";

.node-details-container {
  .top-part {
    justify-content: space-between;
    margin-bottom: 1rem;
  }

  .advanced-button-container {
    // mobile
    @media only screen and (max-width: $mobile-screen-width) {
      width: 100%;
      margin-top: 10px;
    }
  }
  .advanced-button {
    height: 35px;
    display: flex;
    align-items: center;
  }
}

.advanced-details-modal-content {
  background-color: white;
  display: flex;
  flex-direction: column;
  border-radius: 12px;
  padding: 20px;
  justify-content: space-between;
  .subtitle {
    text-align: center;
  }
  .advanced-details {
    margin-top: 10px;

    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding-left: 5px;
  }
  .advanced-paragraph {
    margin-bottom: 5px;
    display: flex;

    .item-title {
      color: $stratos-green-ligth;
      width: 20%;
      min-width: 130px;
    }
    .item-text {
      padding-left: 10px;
    }
  }
}
