@import "../../../styles/setup.scss";

.traffic-info-container {
  margin-bottom: 1rem;

  .traffic-title-container {
    margin-bottom: 10px;

    .traffic-title {
      font-weight: 500;
      font-size: 24px;
      font-family: $equal-font;
      font-style: normal;
      margin-right: 20px;

      line-height: 36px;
    }
  }

  .traffic-data-container {
    @media only screen and (max-width: $mobile-screen-width) {
      flex-wrap: wrap;
      justify-content: center;
    }
    .cards-container {
      margin-right: 5px;
      width: 20%;
      min-width: 260px;
      @media only screen and (max-width: $mobile-screen-width) {
        width: 100%;
      }
    }
    .graph-container {
      border-radius: 12px;
      border: 1px solid $stratos-border-color;

      width: 80%;
      max-height: 345px;
      @media only screen and (max-width: $mobile-screen-width) {
        min-height: 300px;
        max-height: 350px;
        width: 100%;
      }
    }
  }
}
